body {
  background-color: #f4f4f4;
  flex-direction: column;
  height: 100%;
  font-family: Arial, sans-serif;
  display: flex;
}

body:before {
  content: "";
  filter: blur(4px);
  z-index: -1;
  background-image: url("FondoMenu7580.7b169f2f.avif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

body:after {
  content: "";
  z-index: -1;
  background-color: #0000004d;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

main {
  flex: 1;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  display: flex;
}

.calendar-container {
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  width: 80%;
  max-width: 1200px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.week-header {
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
  font-size: 24px;
}

.days-container {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 10px;
  display: flex;
}

.day {
  text-align: center;
  box-sizing: border-box;
  background-color: #fffc;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: calc(14.2857% - 20px);
  padding: 20px;
  display: flex;
}

.day-name {
  color: #000;
  margin-bottom: 10px;
  font-size: 18px;
}

.start-button {
  color: #fff;
  cursor: pointer;
  cursor: pointer;
  text-align: center;
  background-color: #ff3b30;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 200px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.start-button button {
  color: #fff;
  font-weight: bold;
}

.start-button:hover {
  background-color: #ff6b81;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px #00000026;
}

.start-button:active {
  background-color: #ff4757;
  transform: translateY(0);
  box-shadow: 0 4px 6px #0000001a;
}

.check-icon {
  color: green;
  margin-top: 5px;
  font-size: 20px;
}

@media (width >= 1800px) {
